import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SolicitationsListComponent } from './solicitations-list.component';
import { solicitationsRouting } from './solicitations-list.rounting';
import { SharedModule } from '../shared/shared.module';
import { FormService } from '../services/form.service';
import { PdfReaderModule } from '../pdf-reader/pdf-reader.module';
import {SearchFormModule} from '../search-form/search-form.module';

@NgModule({
  imports: [
    CommonModule,
    solicitationsRouting,
    SharedModule,
    PdfReaderModule,
    SearchFormModule
  ],
  declarations: [SolicitationsListComponent],
  providers: [
    FormService
  ]
})
export class SolicitationsListModule { }



