import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { CrudService } from './crud.service';
import { Observable } from 'rxjs/Observable';
import { Solicitation } from '../models/solicitation.model';
import { SolicitationParser } from '../parsers/solicitation.parser';
import 'rxjs/add/observable/throw';

@Injectable()
export class SearchService {
  constructor(private http: Http, private crudService: CrudService) {

  }

searchSolicitation(model: Solicitation): any {
  if (model.travelDate) {
    model.travelDate = new Date(model.travelDate).toISOString();
  } else {
    model.travelDate = '';
  }
  if (model.returnDate) {
    model.returnDate = new Date(model.returnDate).toISOString();
  } else {
    model.returnDate = '';
  }
    return this.crudService.getWithParameter('https://mobilidade-ufcg.herokuapp.com/form/search', model).map((response) => {
      return response;
    });
  }
}



