import { Component, OnInit } from '@angular/core';
import { FormService } from '../services/form.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-solicitations-list',
  templateUrl: './solicitations-list.component.html',
  styleUrls: ['./solicitations-list.component.scss']
})
export class SolicitationsListComponent implements OnInit {

  protected isLoadingSolicitations: boolean;
  solicitations: { header: string[]; itemsContent: any[]; };

  constructor(private formService: FormService,
              private router: Router) {
                this.isLoadingSolicitations = true;
  }

  ngOnInit() {
    this.solicitations = {
      header: ['Requisitante', 'Telefone', 'Data', 'Destino'],
      itemsContent: []
    };
    this.showSolicitations();
  }

  showSolicitations() {
    this.solicitations.itemsContent = [];
    this.formService.solicitationObs.subscribe((data) => {
      for (const solicitationData of data) {
          this.solicitations.itemsContent.push(solicitationData);
      }
      this.isLoadingSolicitations = false;
    });
  }

  goToFull(solicitation) {
    this.router.navigate(['/solicitation', {id: solicitation.id}]);
  }

  newSolicitation(value: any) {
    this.showSolicitations();
  }

  searchResult(value: any) {
    this.solicitations.itemsContent = [];
    for (const solicitationData of value) {
          this.solicitations.itemsContent.push(solicitationData);
    }
  }
}



