import { RouterModule, Routes } from '@angular/router';
import { SolicitationsListComponent } from './solicitations-list.component';
import { ModuleWithProviders } from '@angular/core';

const solicitationsRoutes: Routes = [
    {
        path: '', component: SolicitationsListComponent
    }
];

export const solicitationsRouting: ModuleWithProviders = RouterModule.forChild(solicitationsRoutes);



